* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Bruno Ace SC, cursive, sans-serif;
}

body {
  text-align: center;
  -ms-user-select: none;
  user-select: none;
  background-image: url("data:image/png;base64,");
  margin-top: 13.5rem;
  font: 400 16px / 1.5 exo, ubuntu, segoe ui, helvetica, arial, sans-serif;
  animation: bg-scrolling .32s linear infinite;
  overflow: hidden;
}

.timer-bg {
  color: rgba(154, 154, 154, .1);
  background-attachment: fixed;
  outline: none;
  justify-content: space-between;
  font-size: 300px;
  display: none;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@media (max-width: 1400px) {
  .timer-bg {
    font-size: 200px;
  }
}

@media (max-width: 700px) {
  .timer-bg {
    font-size: 100px;
  }
}

@media (max-width: 400px) {
  .timer-bg {
    font-size: 50px;
  }
}

.circle {
  width: 200px;
  height: 200px;
  cursor: pointer;
  z-index: 10000;
  border: 8px solid #fff;
  border-radius: 100%;
  justify-content: center;
  align-items: center;
  animation: puff-in-center .5s cubic-bezier(.55, .085, .68, .53) both;
  display: none;
  position: absolute;
  transform: translate(-50%, -50%);
  box-shadow: 9px 9px 44px 16px #42445a;
}

.circle-event {
  width: 150px;
  height: 150px;
  cursor: pointer;
  z-index: 1000;
  background: linear-gradient(-45deg, #ee7752, #e73c7e, #23a6d5, #23d5ab) 0 0 / 200% 200%;
  border: 5px dashed #fff;
  border-radius: 100%;
  justify-content: center;
  align-items: center;
  animation: gradient 1.4s ease-in, puff-in-center .5s cubic-bezier(.55, .085, .68, .53) both;
  display: none;
  position: absolute;
  transform: translate(-50%, -50%);
  box-shadow: 9px 9px 44px 16px #d11ed7;
}

@media (max-width: 1600px) {
  .circle, .circle-event {
    width: 180px;
    height: 180px;
    border: 5px solid #fff;
  }
}

@media (max-width: 1300px) {
  .circle, .circle-event {
    width: 150px;
    height: 150px;
    border: 5px solid #fff;
  }
}

@media (max-width: 1000px) {
  .circle, .circle-event {
    width: 120px;
    height: 120px;
    border: 4px solid #fff;
  }
}

@media (max-width: 700px) {
  .circle, .circle-event {
    width: 100px;
    height: 100px;
    border: 4px solid #fff;
  }
}

@media (max-width: 400px) {
  .circle, .circle-event {
    width: 70px;
    height: 70px;
    border: 1px solid #fff;
  }
}

@keyframes gradient {
  0% {
    background-position: 0 0;
  }

  100% {
    background-position: 100% 100%;
  }
}

.score {
  color: #fff;
  font-size: 40px;
  font-weight: 700;
}

@media (max-width: 700px) {
  .score {
    font-size: 20px;
  }
}

.game-over {
  width: 100%;
  height: 300px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 30%;
}

.final-score {
  color: #9a9a9a;
  font-size: 70px;
  font-weight: 400;
  animation: puff-in-center .3s ease-in;
}

@media (max-width: 700px) {
  .final-score {
    font-size: 40px;
  }
}

.record {
  color: #9a9a9a;
  font-size: 20px;
  font-weight: 400;
}

.play-again {
  width: 300px;
  cursor: pointer;
  color: #777;
  background-color: rgba(119, 119, 119, .15);
  border: none;
  border-radius: 6px;
  margin-top: 20px;
  padding: 20px;
  font-size: 30px;
  font-style: italic;
}

audio {
  display: none;
}

.audio-interface {
  width: 300px;
  justify-content: space-around;
  margin-top: 30px;
  padding: 10px;
  display: flex;
}

.pause-resume {
  width: 40px;
  color: #777;
  cursor: pointer;
  background-color: rgba(119, 119, 119, .15);
  border: none;
  border-radius: 6px;
  padding: 5px;
  scale: 1.5;
}

.volume {
  cursor: pointer;
}

.color-mode {
  gap: 40px;
  margin-top: 20px;
  display: flex;
}

.lightmode {
  width: 50px;
  height: 50px;
  cursor: pointer;
  background-color: #fff;
  border: 2px solid #a9a9a9;
  border-radius: 100%;
}

.darkmode {
  width: 50px;
  height: 50px;
  cursor: pointer;
  background-color: #000;
  border: 2px solid #a9a9a9;
  border-radius: 100%;
}

.view-ranking {
  width: 50px;
  height: 50px;
  cursor: pointer;
  border: 2px solid #a9a9a9;
  border-radius: 100%;
  overflow: hidden;
}

.email-input, .password-input, .name-input {
  color: #777;
  cursor: pointer;
  background-color: rgba(119, 119, 119, .15);
  border: none;
  border-radius: 6px;
  margin: 15px;
  padding: 10px;
}

.login-btn, .register-btn {
  width: 100px;
  color: #777;
  cursor: pointer;
  background-color: rgba(119, 119, 119, .15);
  border: none;
  border-radius: 6px;
  margin: 15px;
  padding: 10px;
}

.set-up-text {
  color: #9a9a9a;
  margin-top: 50px;
  font-size: 20px;
  font-weight: 400;
}

.rank-list {
  border-top: 2px solid #9a9a9a;
  border-bottom: 2px solid #9a9a9a;
  padding: 20px;
  list-style: none;
  position: absolute;
  right: 50px;
}

@media (max-width: 1440px) {
  .rank-list {
    display: none;
  }
}

.rank-list-record {
  color: #9a9a9a;
  justify-content: space-between;
  gap: 40px;
  margin: 10px;
  display: flex;
}

.rank-number {
  font-size: large;
  font-weight: bold;
}

@keyframes puff-in-center {
  0% {
    filter: blur(4px);
    opacity: 0;
    transform: scale(2);
  }

  100% {
    filter: blur();
    opacity: 1;
    transform: scale(1);
  }
}

@keyframes bg-scrolling {
  100% {
    background-position: 0 50px;
  }
}

@keyframes bg-scrolling-dark {
  100% {
    background-position: 0 225px;
  }
}

@keyframes vanish {
  0% {
    opacity: 1;
  }

  100% {
    opacity: 0;
  }
}

